import React, { useState } from "react";

// Helper for base64 conversion
function fileToDataUrl(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = (e) => resolve(e.target.result);
    reader.onerror = (e) => reject(e);
    reader.readAsDataURL(file);
  });
}

export function CardGallery({
  cards,
  updateOwner,
  updateTags,
  deleteCard,
  updateImage, // <-- NEW PROP
  sortBy,
  setSortBy,
  filterRarity,
}) {
  const [selectedImage, setSelectedImage] = useState(null);
  const [editingCard, setEditingCard] = useState(null);
  const [newTags, setNewTags] = useState("");
  const [newTier, setNewTier] = useState("");
  const [newOwner, setNewOwner] = useState("");
  const [newImageFile, setNewImageFile] = useState(null);
  const [newImageUrl, setNewImageUrl] = useState("");
  const [showWildCards, setShowWildCards] = useState(true);
  const [sortAsc, setSortAsc] = useState(true);
  const [confirmDelete, setConfirmDelete] = useState(null);

  const defaultImage =
    "https://media.discordapp.net/attachments/1155772738858913792/1371485967667429427/image.webp?ex=6846e7d7&is=68459657&hm=90eec4cb4ec385322895257f466e0415ef5fdb4d288a3a87e4b2269c3cbc5a4d&=&format=webp";

  const applySortAndFilter = (cards) => {
    const filtered = cards.filter(
      (card) => filterRarity === "all" || card.tier === filterRarity
    );
    const sorted = filtered.sort((a, b) => {
      if (sortBy === "id") {
        const comp = a.id.localeCompare(b.id);
        return sortAsc ? comp : -comp;
      }
      if (sortBy === "tier") {
        const order = [
          "common",
          "rare",
          "epic",
          "legendary",
          "mystic",
          "celestial",
        ];
        return order.indexOf(a.tier) - order.indexOf(b.tier);
      }
      if (sortBy === "owner") {
        return (a.owner || "none").localeCompare(b.owner || "none");
      }
      return 0;
    });
    return sorted;
  };

  const wildCards = applySortAndFilter(cards.filter((card) => !card.image));
  const ownedCards = applySortAndFilter(cards.filter((card) => card.image));
  const displayedCount = [...ownedCards, ...(showWildCards ? wildCards : [])]
    .length;

  const uniqueOwners = [
    ...new Set(
      cards.map((card) => card.owner).filter((o) => o && o !== "none")
    ),
  ];

  // Edit Modal: Prefill with card data
  const handleEditClick = (card) => {
    setEditingCard(card);
    setNewTags(card.tags.join(", "));
    setNewTier(card.tier);
    setNewOwner(card.owner || "");
    setNewImageFile(null);
    setNewImageUrl(card.image || "");
  };

  // Save changes (including optional image change)
  const handleSave = async () => {
    let imageResult = newImageUrl;
    if (newImageFile) {
      imageResult = await fileToDataUrl(newImageFile);
    }
    if (editingCard) {
      updateTags(
        editingCard.id,
        newTags.split(",").map((t) => t.trim())
      );
      updateOwner(editingCard.id, newOwner.trim() || "none");
      if (typeof updateImage === "function") {
        updateImage(editingCard.id, imageResult);
      }
      // Optionally update tier as well:
      if (editingCard.tier !== newTier) {
        // Add an updateTier function if you want to allow this edit
      }
    }
    setEditingCard(null);
  };

  // --- Render Card ---
  const renderCard = (card) => {
    const imagePath = card.image || `/cards/${card.id}.webp`;
    return (
      <div
        key={card.id}
        className={`card card-${card.tier} ${
          card.owner === "none" || !card.owner ? "unowned" : ""
        }`}
      >
        <h3
          className="text-lg font-bold mb-1"
          style={{ fontFamily: "'Fira Code', monospace" }}
        >
          {card.id.toString().padStart(5, "0")}
        </h3>
        <p className="text-xs font-semibold text-gray-300 mb-2">
          {card.tier.toUpperCase()}{" "}
          {{
            common: "🥬",
            rare: "🌸",
            epic: "💎",
            legendary: "👑",
            mystic: "🦄",
            celestial: "💫",
          }[card.tier] || ""}
        </p>
        <img
          src={imagePath}
          alt={`Card ${card.id}`}
          className="cursor-pointer rounded-md mb-2"
          onClick={() => setSelectedImage(imagePath)}
          onError={(e) => {
            e.target.onerror = null;
            e.target.src = defaultImage;
          }}
        />
        <p>
          Owner:{" "}
          {card.owner && card.owner.trim() !== "" && card.owner !== "none"
            ? card.owner
            : "None"}
        </p>
        <p className="tags mt-1">
          {card.tags && card.tags.length > 0 ? card.tags.join(", ") : "—"}
        </p>
        <div className="actions mt-2 flex flex-wrap gap-2 w-full">
          <button
            onClick={() => handleEditClick(card)}
            className="flex-1 min-w-0"
          >
            Edit
          </button>
          <button
            onClick={() => setConfirmDelete(card.id)}
            className="flex-1 min-w-0 text-red-400"
          >
            Delete
          </button>
        </div>
      </div>
    );
  };

  return (
    <>
      <div className="flex items-center justify-between mb-2">
        <h2 className="text-xl font-bold">
          Displaying {displayedCount} Card{displayedCount !== 1 ? "s" : ""}
        </h2>
        <button onClick={() => setSortAsc((prev) => !prev)} className="text-sm">
          Sort ID {sortAsc ? "▲" : "▼"}
        </button>
      </div>

      {ownedCards.length > 0 && (
        <>
          <h3 className="text-lg font-semibold mb-2">Owned Cards</h3>
          <div className="card-gallery">{ownedCards.map(renderCard)}</div>
        </>
      )}

      {wildCards.length > 0 && (
        <>
          <div className="flex items-center justify-between mb-2 mt-6">
            <h3 className="text-lg font-semibold">Wild Cards</h3>
            <button
              onClick={() => setShowWildCards((prev) => !prev)}
              className="text-sm underline"
            >
              {showWildCards ? "Hide" : "Show"} Wild Cards
            </button>
          </div>
          {showWildCards && (
            <div className="card-gallery">{wildCards.map(renderCard)}</div>
          )}
        </>
      )}

      {selectedImage && (
        <div className="modal-overlay" onClick={() => setSelectedImage(null)}>
          <div className="modal-content">
            <img
              src={selectedImage}
              alt="Expanded card"
              onError={(e) => {
                e.target.onerror = null;
                e.target.src = defaultImage;
              }}
            />
            <button
              className="close-btn"
              onClick={() => setSelectedImage(null)}
            >
              Close
            </button>
          </div>
        </div>
      )}

      {editingCard && (
        <div className="modal-overlay">
          <div
            className={`modal-content card-${editingCard?.tier || "common"}`}
          >
            <h3 className="text-lg mb-2">
              Edit Card {editingCard.id.toString().padStart(5, "0")}
            </h3>
            <input
              type="text"
              value={newTags}
              onChange={(e) => setNewTags(e.target.value)}
              placeholder="Tags (comma-separated)"
              className="mb-2 w-full"
            />
            <select
              value={newTier}
              onChange={(e) => setNewTier(e.target.value)}
              className="mb-2 w-full"
            >
              <option value="common">Common 🥬</option>
              <option value="rare">Rare 🌸</option>
              <option value="epic">Epic 💎</option>
              <option value="legendary">Legendary 👑</option>
              <option value="mystic">Mystic 🦄</option>
              <option value="celestial">Celestial 💫</option>
            </select>
            <input
              type="text"
              list="owner-list"
              value={newOwner}
              onChange={(e) => setNewOwner(e.target.value)}
              placeholder="Owner"
              className="mb-2 w-full"
            />
            <datalist id="owner-list">
              {uniqueOwners.map((owner, index) => (
                <option key={index} value={owner} />
              ))}
            </datalist>
            {/* --- Change image section --- */}
            <div className="mb-2 w-full">
              <label className="block font-semibold mb-1">
                Change Image (upload file or paste URL)
              </label>
              <input
                type="file"
                accept="image/*"
                onChange={(e) => setNewImageFile(e.target.files[0])}
                className="mb-2 w-full"
              />
              <input
                type="text"
                value={newImageUrl}
                onChange={(e) => {
                  setNewImageUrl(e.target.value);
                  setNewImageFile(null);
                }}
                placeholder="Or paste new image URL"
                className="mb-2 w-full"
              />
              <div>
                <span className="text-xs text-gray-400">
                  (Leave both blank to keep current image)
                </span>
              </div>
            </div>
            <div className="space-x-2 mt-2">
              <button onClick={handleSave}>Save</button>
              <button onClick={() => setEditingCard(null)}>Cancel</button>
            </div>
          </div>
        </div>
      )}

      {confirmDelete && (
        <div className="modal-overlay">
          <div className="modal-content">
            <h3 className="mb-4">
              Are you sure you want to delete card{" "}
              {confirmDelete.toString().padStart(5, "0")}?
            </h3>
            <div className="flex justify-end gap-2">
              <button
                onClick={() => setConfirmDelete(null)}
                className="bg-gray-500"
              >
                Cancel
              </button>
              <button
                onClick={() => {
                  deleteCard(confirmDelete);
                  setConfirmDelete(null);
                }}
                className="bg-red-500"
              >
                Confirm Delete
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
