import React, { useEffect, useState } from "react";
import { db } from "./db";
import CardForm from "./CardForm";
import { CardGallery } from "./CardGallery";

export default function App() {
  const [cards, setCards] = useState([]);
  const [sortBy, setSortBy] = useState("id");
  const [filterRarity, setFilterRarity] = useState("all");
  const [searchQuery, setSearchQuery] = useState("");

  // --- Migration code: runs once when app loads ---
  useEffect(() => {
    const migrate = async () => {
      const oldCards = JSON.parse(localStorage.getItem("myu-cards") || "[]");
      if (oldCards.length) {
        // Check if IndexedDB is empty first to avoid duplicates
        const indexedCards = await db.cards.count();
        if (indexedCards === 0) {
          await db.cards.bulkAdd(oldCards);
          alert(
            `Migrated ${oldCards.length} cards from localStorage to IndexedDB!`
          );
          // Optionally clear localStorage:
          // localStorage.removeItem("myu-cards");
        }
      }
      setCards(await db.cards.toArray());
    };
    migrate();
  }, []);

  // --- CARD CRUD FUNCTIONS ---
  async function addCard(card) {
    await db.cards.add({ ...card, id: card.id.toString() });
    setCards(await db.cards.toArray());
  }

  async function updateOwner(cardId, newOwner) {
    await db.cards.update(cardId, { owner: newOwner });
    setCards(await db.cards.toArray());
  }

  async function updateTags(cardId, newTags) {
    await db.cards.update(cardId, { tags: newTags });
    setCards(await db.cards.toArray());
  }

  async function updateImage(cardId, newImage) {
    await db.cards.update(cardId, { image: newImage });
    setCards(await db.cards.toArray());
  }

  async function deleteCard(cardId) {
    await db.cards.delete(cardId);
    setCards(await db.cards.toArray());
  }

  // --- FILTER & SORT ---
  const filteredCards = cards.filter((card) => {
    const matchesRarity = filterRarity === "all" || card.tier === filterRarity;
    const matchesSearch =
      card.id.toString().includes(searchQuery) ||
      (Array.isArray(card.tags) &&
        card.tags.some((tag) =>
          tag.toLowerCase().includes(searchQuery.toLowerCase())
        ));
    return matchesRarity && matchesSearch;
  });

  const sortedCards = [...filteredCards].sort((a, b) => {
    if (sortBy === "id") {
      return a.id.localeCompare(b.id);
    } else if (sortBy === "tier") {
      const order = [
        "common",
        "rare",
        "epic",
        "legendary",
        "mystic",
        "celestial",
      ];
      return order.indexOf(a.tier) - order.indexOf(b.tier);
    } else if (sortBy === "owner") {
      return (a.owner || "none").localeCompare(b.owner || "none");
    }
    return 0;
  });

  // --- RENDER ---
  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">MyU Card Gallery</h1>

      <CardForm addCard={addCard} existingIds={cards.map((card) => card.id)} />

      <div className="mb-4 flex flex-wrap gap-2 items-center">
        <select
          value={sortBy}
          onChange={(e) => setSortBy(e.target.value)}
          className="p-1 rounded border"
        >
          <option value="id">Sort by ID</option>
          <option value="tier">Sort by Rarity</option>
          <option value="owner">Sort by Owner</option>
        </select>

        <select
          value={filterRarity}
          onChange={(e) => setFilterRarity(e.target.value)}
          className="p-1 rounded border"
        >
          <option value="all">Show All</option>
          <option value="common">Common 🥬</option>
          <option value="rare">Rare 🌸</option>
          <option value="epic">Epic 💎</option>
          <option value="legendary">Legendary 👑</option>
          <option value="mystic">Mystic 🦄</option>
          <option value="celestial">Celestial 💫</option>
        </select>

        <input
          type="text"
          placeholder="Search by ID or tag..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          className="p-1 rounded border"
        />
      </div>

      <CardGallery
        cards={sortedCards}
        updateOwner={updateOwner}
        updateTags={updateTags}
        updateImage={updateImage}
        deleteCard={deleteCard}
        sortBy={sortBy}
        filterRarity={filterRarity}
      />
    </div>
  );
}
