import React, { useState } from "react";
import { createWorker } from "tesseract.js";

export default function CardForm({ addCard, existingIds = [] }) {
  const [id, setId] = useState("");
  const [owner, setOwner] = useState("");
  const [tier, setTier] = useState("common");
  const [tags, setTags] = useState("");
  const [image, setImage] = useState("");
  const [imageFile, setImageFile] = useState(null);

  // Persistent owner history for datalist
  const [ownerHistory, setOwnerHistory] = useState(() =>
    Array.from(new Set(JSON.parse(localStorage.getItem("myu-owners") || "[]")))
  );

  function clearForm() {
    setId("");
    setOwner("");
    setTier("common");
    setTags("");
    setImage("");
    setImageFile(null);
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    const paddedId = id.padStart(5, "0");
    if (existingIds.includes(paddedId)) {
      alert("Card ID already exists.");
      return;
    }

    let imageUrl = image.trim();
    if (imageFile) {
      // Convert the file to base64 data URL
      imageUrl = await fileToDataUrl(imageFile);
    }

    const trimmedOwner = owner.trim() === "" ? "none" : owner.trim();

    // Only add owner to history on submit (as in your previous fix)
    if (trimmedOwner !== "none" && !ownerHistory.includes(trimmedOwner)) {
      const updated = [trimmedOwner, ...ownerHistory].slice(0, 20);
      setOwnerHistory(updated);
      localStorage.setItem("myu-owners", JSON.stringify(updated));
    }

    addCard({
      id: paddedId,
      owner: trimmedOwner,
      tier,
      tags: tags.split(",").map((tag) => tag.trim()),
      image: imageUrl,
    });
    clearForm();
  };

  // Card image file input, autofill ID with filename
  const handleCardImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setImageFile(file);
      setImage(""); // clear URL
      const filename = file.name.split(".").slice(0, -1).join(".");
      setId(filename);
    }
  };

  return (
    <form
      onSubmit={handleSubmit}
      className="flex flex-wrap gap-2 items-center mb-4 card-form-row"
      style={{ alignItems: "center" }}
    >
      <input
        type="text"
        placeholder="Card ID"
        value={id}
        onChange={(e) => setId(e.target.value)}
        required
        className="p-1 rounded border w-24"
      />

      <input
        type="text"
        placeholder="Owner"
        value={owner}
        onChange={(e) => setOwner(e.target.value)}
        className="p-1 rounded border w-28"
        list="owner-list"
        autoComplete="off"
      />
      <datalist id="owner-list">
        {ownerHistory.map((o, idx) => (
          <option key={idx} value={o} />
        ))}
      </datalist>

      <select
        value={tier}
        onChange={(e) => setTier(e.target.value)}
        className="p-1 rounded border w-32"
      >
        <option value="common">Common 🥬</option>
        <option value="rare">Rare 🌸</option>
        <option value="epic">Epic 💎</option>
        <option value="legendary">Legendary 👑</option>
        <option value="mystic">Mystic 🦄</option>
        <option value="celestial">Celestial 💫</option>
      </select>

      <input
        type="text"
        placeholder="Tags (comma-separated)"
        value={tags}
        onChange={(e) => setTags(e.target.value)}
        className="p-1 rounded border w-40"
      />

      <label className="flex items-center gap-1 w-fit">
        Card Image:
        <input
          type="file"
          accept="image/*"
          onChange={handleCardImageChange}
          className="p-1"
        />
      </label>

      <input
        type="text"
        placeholder="Image URL"
        value={image}
        onChange={(e) => {
          setImage(e.target.value);
          setImageFile(null);
        }}
        className="p-1 rounded border w-40"
      />

      <button type="submit" className="p-1 rounded bg-blue-600 text-white w-24">
        Add Card
      </button>
    </form>
  );
}
function fileToDataUrl(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = (e) => resolve(e.target.result);
    reader.onerror = (e) => reject(e);
    reader.readAsDataURL(file);
  });
}
