import Dexie from "dexie";

export const db = new Dexie("MyUCardDB");

// Define the schema (your card object structure)
db.version(1).stores({
  cards: "id, owner, tier, tags, image",
  // 'id' is primary key, you can index more fields if you want
});

// Optional: add a helper to clear everything
export const clearAllCards = async () => {
  await db.cards.clear();
};
